@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  color-scheme: light;
}

body {
  @apply bg-white text-gray-900;
}

/* Custom utility classes for mobile optimization */
.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Smooth touch scrolling for mobile */
.smooth-touch {
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
}

/* Better touch targets */
.touch-target {
  min-height: 44px;
  min-width: 44px;
}

/* Mobile-safe text truncation */
.text-truncate-mobile {
  @apply truncate;
  max-width: 120px;
}

/* Enhanced mobile transitions */
.mobile-transition {
  @apply transition-all duration-200 ease-out;
}

/* Safe area support for iPhone X and newer devices */
.safe-area-inset-bottom {
  padding-bottom: env(safe-area-inset-bottom, 0);
}

/* Additional bottom navigation styles */
.bottom-nav-safe {
  padding-bottom: max(env(safe-area-inset-bottom, 0), 8px);
}

/* Multilayer Tab Navigation Styles */
.multilayer-tab {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.tab-active-indicator {
  background: linear-gradient(90deg, transparent, currentColor, transparent);
  background-size: 200% 100%;
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { background-position: -200% center; }
  100% { background-position: 200% center; }
}

.tab-hover-glow {
  position: relative;
  overflow: hidden;
}

.tab-hover-glow::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s;
}

.tab-hover-glow:hover::before {
  left: 100%;
}